import Notify from './notify.esm';

export function WebNotification() {
    this.notify = this.init();
}

WebNotification.prototype.init = function () {
    window.Notification && "granted" !== window.Notification.permission && window.Notification.requestPermission();
    if (this.notify) {
        return this.notify;
    }
    let notify = new Notify({
        effect: 'scroll', // flash | scroll, Flashing or scrolling
        interval: 1000,
    });
    notify.onclick = function (event) {
        window.focus();
    };
    return notify;
}

WebNotification.prototype.sendNotification = function (message) {
    let body;
    let type = message.type;
    if (type === 'image') {
        body = "[]";
    } else if (type === 'video') {
        body = "[]";
    } else if (type === 'file') {
        body = "[]";
    } else if (type === 'text') {
        let text = message.payload.text;
        if (text.length >= 50) {
            body = text.substring(0, 30) + "...";
        } else {
            body = text;
        }
    } else if (type === "CS_ACCEPT") {
        body = message.senderData.name + "";
    } else if (type === "CS_END") {
        body = message.senderData.name + "";
    } else if (type === "CS_TRANSFER") {
        body = message.senderData.name + "" + message.payload.transferTo.data.name;
    }
    let notification = {
        title: message.senderData.name,
        body: body
    }
    let tabHidden = document.hidden;
    if (tabHidden && this.notify) {
        let title = notification.title;
        let body = notification.body;
        this.notify.setTitle(true);
        this.notify.setTitle("[]");
        this.notify.notify({
            title: title,
            body: body,
            icon: "https://www.goeasy.io/favicon.ico"
        });
    }
}

WebNotification.prototype.clearNotificationTitle = function () {
    if (this.notify) {
        this.notify.setTitle();
    }
}