import utils from "../utils/utils";
import axios from 'axios';

const env = process.env.VUE_APP_ENV;
const whiteList = [
    "/api/v2/cs-customer/public"
]

function isWhiteList(requestUrl) {
    for (let i = 0; i < whiteList.length; i++) {
        let url = whiteList[i];
        if (requestUrl.includes(url)) {
            return true;
        }
    }
    return false;
}

function checkAndRefreshToken() {
    let xhr;
    if (window.XMLHttpRequest) {
        xhr = new XMLHttpRequest();
    } else {
        xhr = new ActiveXObject("Microsoft.XMLHttp")
    }
    let url = `https://${env}sso.goeasy.io`;
    let expiresAt = utils.getCookie(`goeasy.token.expiresAtMillis.${env}`);
    let currentTime = new Date().getTime();
    if (xhr && expiresAt - currentTime < 3600 * 1000) {//less than 1 hour
        let refreshToken = utils.getRefreshToken();
        xhr.open("post", url + "/refreshtoken", true);
        xhr.setRequestHeader('content-type', 'application/x-www-form-urlencoded');
        xhr.send("refreshToken=" + refreshToken);
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    let result = JSON.parse(xhr.responseText);
                    let newToken = result.token;
                    let newRefreshToken = result.refreshToken;
                    let expiresAtMillis = result.expiresAtMillis;
                    utils.storeTokens(newToken, newRefreshToken, expiresAtMillis);
                } else {
                    // console.log("xxxx error: ", xhr.status)
                }
            } else {
                // console.log(xhr.status);
            }
        }
    }
}

const http = axios.create({
    baseURL: '',
    timeout: 60000
});

// 
http.interceptors.request.use((config) => {
    let requestUrl = config.url;
    let skipValidation = isWhiteList(requestUrl);
    if (!skipValidation) {
        checkAndRefreshToken();
        const token = utils.getAccessToken();
        if (token) {
            config.headers['Authorization'] = "Bearer " + token
        }
    }
    return config;
}, (error) => {
    return Promise.reject(error);
});

// 
http.interceptors.response.use((response) => {
    return response.data;
}, (error) => {
    return Promise.reject(error);
});

export default http;
