class Utils {

    getAnonymousRandomGuid() {
        return 'anonymous' + this.getRandomGuid();
    }

    getAccessToken() {
        let env = process.env.VUE_APP_ENV;
        let accessToken = this.getCookie(`goeasy.token.${env}`);
        return accessToken;
    }

    getRefreshToken() {
        let env = process.env.VUE_APP_ENV;
        let refreshToken = this.getCookie(`goeasy.refreshToken.${env}`);
        return refreshToken;
    }

    storeTokens(token, refreshToken, expiresAtMillis) {
        let env = process.env.VUE_APP_ENV;
        this.setCookie(`goeasy.token.${env}`, token);
        this.setCookie(`goeasy.refreshToken.${env}`, refreshToken);
        this.setCookie(`goeasy.token.expiresAtMillis.${env}`, expiresAtMillis + "");
    }

    setCookie(name, value) {
        console.log("will set " + name + "with value: " + value);
        document.cookie = name + "=" + escape(value) + ";domain=goeasy.io";
    }

    getRandomGuid() {
        return '-xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            let r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    getCookie(name) {
        let arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        if (arr = document.cookie.match(reg)) {
            return unescape(arr[2]);
        }
        return null;
    }
}

export default new Utils();

